<template>
<a href="javascript:;" class="rule" @click="showRuleModal">
  活动规则
</a>
</template>

<script>
export default {
  name: 'CommonRule',
  data() {
    return {}
  },
  methods: {
    showRuleModal() {
      this.$emit('showRuleModal')
    }
  }
}
</script>

<style lang="less" scoped>
.rule {
  width: .98rem;
  height: .4rem;
}
</style>



